#footer {
	position: fixed;
    bottom: 5px;
    right: 5px;
    font-size: 0.75rem;
}
#dashboard .dashboard--card-list {
	width: 83%;
	display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
}
#dashboard .dashboard--card {
	width: 24%;
	height: 350px;
  margin: 0 0.5% 1% 0.5%;
  border-radius: 9px;
  overflow: hidden;
  background: white;
  transition: 0.5s all;
}
#dashboard .dashboard--card:hover,
#dashboard .dashboard--card:focus {
	box-shadow: 0 0 15px rgba(0, 0, 0, 20%);
	-webkit-transform: translateY(-8px);
	        transform: translateY(-8px);
}


#dashboard .dashboard--card.empty {
	display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: relative;
}
#dashboard .dashboard--card.empty:after {
	content: '';
	opacity: 0;
	background-image: url(/static/media/card_background.851e805a.svg);
	background-size: cover;
	background-repeat: no-repeat;
	transition: 0.5s all;
	position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
}
#dashboard .dashboard--card.empty:hover,
#dashboard .dashboard--card.empty:empty {
	color: white;
}
#dashboard .dashboard--card.empty:hover:after,
#dashboard .dashboard--card.empty:empty:after {
	opacity: 1;
}
#dashboard .dashboard--card.empty i {
	font-size: 3rem;
}


#dashboard .dashboard--card .dashboard--card--image-wrapper {
	height: 150px;
	width: 100%;
	background: gray;
	position: relative;
}
#dashboard .dashboard--card .dashboard--card--content {
	padding: 24px;
}

#dashboard .dashboard--card .dashboard--card--content .dashboard--card--header {
	display: flex;
	justify-content: space-between;
}
#dashboard .dashboard--card .dashboard--card--content .dashboard--card--header h4 {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

#dashboard .dashboard--card .dashboard--card--content .dashboard--card--metadata {
	font-size: 0.875rem;
}


#dashboard .dashboard--card .dashboard--card--image-wrapper .settings {
	position: absolute;
	top: 5px;
	right: 5px;
}
#dashboard .dashboard--card .dashboard--card--image-wrapper .settings:hover ul {
	display: block;
}
#dashboard .dashboard--team-list {}
#dashboard .dashboard--team-list > li {
	transition: 0.5s all;
	cursor: pointer;
	
	display: flex;
	justify-content: space-between;
	align-items: center;
	min-height: 17px;
	position: relative;
}
#dashboard .dashboard--team-list > li:hover,
#dashboard .dashboard--team-list > li:focus {
	opacity: 0.65;
	margin-left: 8px;
}
#dashboard .dashboard--team-list > li.active {
	font-weight: 700;
	color: #008075;
}
#dashboard .dashboard--team-list > li.active:hover,
#dashboard .dashboard--team-list > li.active:focus {
	margin-left: 0;
	opacity: 1;
}

#dashboard .dashboard--team-list > li .settings:hover > ul, 
#dashboard .dashboard--team-list > li .settings:focus > ul {
	display: block;
}

#dashboard {
	background: url(/static/media/blob_large-hero.710e8780.svg);
	min-height: 100vh;
  background-repeat: no-repeat;
  background-size: 75%;
  background-position: 200% -500%;
}
#dashboard main {	
	display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
    
	padding: 72px 24px 24px;
}
#dashboard main nav {
	width: 15%;
	margin-right: 2%;
}
#workshop .workshop--stage {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 100vh;
}

.workshop--stage .board--list--arrow-prev,
.workshop--stage .board--list--arrow-next {
	position: fixed;
  top: 50%;
  padding: 16px;
  margin-top: -24px;
  font-size: 2rem;
  z-index: 9;
}

.workshop--stage .board--list--arrow-next {
	right: 0;
}
.board--list {
	display: flex;
  justify-content: flex-start;
}

.board--wrapper {
	width: 960px;
	margin: 0 48px;
}
.board--wrapper .board--header {
	display: flex;
	justify-content: space-between;
	margin-bottom: 8px;
}
.board--wrapper .board {
	width: 100%;
	height: 600px;
	background: white;
}

#workshop .agenda {
	max-width: 1080px;
	padding: 72px 0;
	margin: 0 auto;
}

#workshop .agenda .agenda--day-list {
	position: relative;
}

#workshop .agenda .agenda--day {
	background: white;
	padding: 24px;
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
	margin: 0 0 24px;
}
#workshop .agenda .agenda--day .draggable {
	position: absolute;
  left: -30px;
  font-size: 1.25rem;
}

#workshop .agenda .agenda--day .agenda--day--header {
	display: flex;
  justify-content: space-between;
  align-items: center;
}

#workshop .agenda .agenda--day .agenda--day--activity-list {
  position: relative;
}

#workshop .agenda .activities--popup {
	position: absolute;
  display: flex;
  background: white;
  top: 100%;
  margin-top: 10px;
  padding: 16px;
  z-index: 9;
  box-shadow: 0 0 10px rgb(0, 0, 0, 0.25);
}
#workshop .agenda .activities--popup .activities--popup--col {
	margin: 0 8px;
	text-align: left;
}
#workshop .agenda .agenda--day .agenda--day--activity-list .agenda--day--activity {
	margin: 0 0 16px 32px;
  padding: 16px;
  position: relative;
  border-bottom: 1px solid #ddd;
}



html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	font-family: 'Roboto', sans-serif;
	font-weight: 400;
	vertical-align: baseline;
	box-sizing: border-box;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
