html,
body {
	background: #F1F2F2;
	min-height: 100%;
}
.btn {
	border-radius: 99px;
	background: white;
	color: #008075;
	border: none;
  padding: 8px 14px;
  font-weight: 600;
  font-size: 0.875;
  transition: 0.5s all;
  cursor: pointer;
  text-decoration: none;
}
.btn:hover,
.btn:focus {
	background: #f1f2f2;
	box-shadow: 0 3px 8px rgba(0,0,0,0.1);
}

.btn-sm {
	font-size: 0.75rem;
	font-weight: 700;
  padding: 6px 10px;
}
.btn-lg {
	font-size: 1rem;
  padding: 10px 20px;
}


.btn-primary {
	background: #008075;
	border: 2px solid #008075;
	color: white;
}
.btn-primary:hover,
.btn-primary:focus {
	background: #02403B;
	border: 2px solid #02403B;
}

.btn-secondary {
	background: #666;
	border: 2px solid #666;
	color: white;
}
.btn-secondary:hover,
.btn-secondary:focus {
	background: #444;
	border: 2px solid #444;
}

.btn-danger {
	background: #E35A25;
	border: 2px solid #E35A25;
	color: white;
}
.btn-danger:hover,
.btn-danger:focus {
	background: #96331F;
	border: 2px solid #96331F;
}


.btn-icon {
	color: black;
	background: none;
	border: none;
	box-shadow: none;
	padding: 8px 10px;
}
.btn-icon:hover,
.btn-icon:focus {
	color: #008075;
	background: none;
	border: none;
	box-shadow: none;
}


.btn-outline-primary {
	background: #ffffff;
	border: 2px solid #008075;
	color: #008075;
}
.btn-outline-primary:hover,
.btn-outline-primary:focus {
	background: #008075;
	color: #FFFFFF;
}

.btn-outline-secondary {
	background: #ffffff;
	border: 2px solid #666666;
	color: #666666;
}
.btn-outline-secondary:hover,
.btn-outline-secondary:focus {
	background: #666666;
	color: #FFFFFF;
}

.btn-outline-danger {
	background: #ffffff;
	border: 2px solid #E35A25;
	color: #E35A25;
}
.btn-outline-danger:hover,
.btn-outline-danger:focus {
	background: #E35A25;
	color: #FFFFFF;
}


.btn-text-primary {
	color: #008075;
}
.btn-text-secondary {
	color: #666666;
}
.btn-text-danger {
	color: #E35A25;
}

