input {
	font: inherit;
	font-family: 'Roboto', sans-serif;
	font-weight: 400;
}


.text-sm {
	font-size: 85%;
}

.text-italic {
	font-style: italic;
}

.text-muted {
	color: #666666;
}
.text-danger {
