}
.header {
	position: fixed;
	display: flex;
	top: 0;
	width: 100vw;
	padding: 16px 24px;
	z-index: 9;
}

.header--group {
	display: flex;
}
.header--group--right {
	margin-left: auto;
}
.header--group > * {
	margin-right: 8px;
}
.header--group > *:last-child {
	margin-right: 0;
}


.header--pill-wrapper {
	position: relative;
}
.header--pill-wrapper:hover ul,
.header--pill-wrapper:focus ul {
	display: block;
}
.header--pill {
	background: white;
  padding: 10px 16px;
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0 0 10px rgba(0,0,0,0.05);
  height: 40px;
  
  display: flex;
  justify-content: center;
  align-items: center;
}
.header--pill.square {
	width: 42px;
}
.header--pill.no-padding {
	padding: 0;
}

.modal {
	position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0,0,0,0.65);
  transition: 0.5s all;
  z-index: 99;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.modal.in {
	-webkit-transform: scale(1);
	        transform: scale(1);
}

.modal .modal--header {
	margin: 72px auto 16px;
	display: flex;
	justify-content: space-between;
}
.modal .modal--container {
	margin: 0 auto 72px;
  background: white;
  padding: 24px;
  border-radius: 9px;
  box-shadow: 0 0 10px rgb(0 0 0 / 10%);
}

.modal .modal--header,
.modal .modal--container {
	width: 600px;
}

.modal.modal-lg .modal--header,
.modal.modal-lg .modal--container {
	width: 960px;
}

.modal .modal--header h2 {
	color: white;
}

.modal .modal--close {
	border: none;
	background: none;
	font-size: 1.25rem;
	color: white;
	transition: 0.5s all;
	cursor: pointer;
}
.modal .modal--close:hover,
.modal .modal--close:focus {
	opacity: 0.65;
}
.alert {
	background: #008075;
	color: white;
	padding: 16px;
	border-radius: 6px;
	display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}
.alert button {
	background: none;
  color: white;
  padding: 0;
  font-size: 1.125rem;
}

.alert.alert-error {
	background-color: #E35A25;
}


.button-group {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	position: relative;
}
.button-group.right {
	justify-content: flex-end;
}

.button-group > * {
	margin: 0 4px;
}
.button-group .btn:first-of-type {
	margin-left: 0;
}
.button-group .btn:last-of-type {
	margin-right: 0;
}
.dropdown {
	position: absolute;
  box-shadow: 0 0 10px rgb(0, 0, 0, 10%);
  border-radius: 6px;
  overflow: hidden;
  display: none;
  z-index: 8;
  color: black;
}
.dropdown.right {
	right: 0;
}

.dropdown > li {
	padding: 12px 16px;
	border-bottom: 1px solid #f1f2f2;
	background: white;
	width: 200px;
}
.dropdown > li:hover,
.dropdown > li:focus {
	background: #f1f2f2;
	cursor: pointer;
}
.dropdown > li:last-of-type {
	border-bottom: none;
}
fieldset {
	display: flex;
  flex-direction: column;
}
fieldset.inline {
	flex-direction: row;
}

