#workshop .workshop--stage {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 100vh;
}

.workshop--stage .board--list--arrow-prev,
.workshop--stage .board--list--arrow-next {
	position: fixed;
  top: 50%;
  padding: 16px;
  margin-top: -24px;
  font-size: 2rem;
  z-index: 9;
}

.workshop--stage .board--list--arrow-next {
	right: 0;
}